import {connect} from "react-redux";
import React, {Component} from "react";
import OraModal from "eshop/modules/core/components/ui/Modal";
import {OraButton} from "eshop/components/OraCommonComponents";
import {getPickupReplanishmentRequiredErrors} from "eshop/modules/checkout/selectors";
import {getCartCode} from "eshop/modules/cart/selectors"
import {dismissBackendValidationErrors, gotoCartSummary, confirmReplanishment} from "../../actions/app";


class PickupReplanishmentErrorView extends Component {

    onClose(){
        this.props.dismiss();
    }

    renderInfo() {
        return (
            <div className="opl-cms">
                <p className="u-font-bold">Niestety zamówiony przez Ciebie sprzęt nie jest dostępny w wybranym salonie.</p>
                <ul>
                    <li>Możesz zmienić salon do obioru sprzętu</li>
                    <li>Możesz wrócić do koszyka i zmienić swój wybór</li>
                    <li>Możesz kontynuować zamówienie i poczekać kilka dni, aż zamówienie zostanie skompletowane w wybranym salonie</li>
                </ul>
            </div>);
    }


    confirmReplanishment() {
        this.props.confirm();
    }

    render() {
        return (<OraModal showClose={true}   open={this.props.errors && this.props.errors.length > 0}  onClose={this.onClose.bind(this)} size="medium">
            <div className="u-padding-all-m u-small-padding-s">
                <div className="l-row u-letter-spacing-xs">
                    <div className="l-col l-col-12">
                        <h4>
                            {"Zamówienie nr "+ this.props.cartCode + " wymaga zatowarowania"}
                        </h4>
                    </div>
                </div>
                <div className="l-row u-spacing-top-m">
                    <div className="l-col l-col-12">
                        {this.renderInfo()}
                    </div>
                </div>
                <div className="l-row u-padding-top-m u-padding-m u-padding-left u-padding-right">
                    <hr/>
                </div>
                <div className="l-row u-spacing-top-m">
                    { !this.props.dismiss ? null :
                        <div className="u-small-hide l-col l-col-medium-2 l-col-3 u-spacing-m u-spacing-top-m">
                            <OraButton type={"text"} onClick={this.props.dismiss}>Zmień salon</OraButton>
                        </div>
                    }
                    {!this.props.goToCart ? null :
                        <div className="l-col l-col-small-12 l-col-medium-6 l-col-5 u-spacing-m u-text-right">
                            <OraButton type={"secondary"} onClick={this.props.goToCart}>Wróć do koszyka</OraButton>
                        </div>
                    }
                    {!this.props.confirm ? null :
                        <div className="l-col l-col-small-12 l-col-medium-4 l-col-4 u-text-right u-spacing-m">
                            <OraButton onClick={this.confirmReplanishment.bind(this)}>Kontynuuj zamówienie</OraButton>
                        </div>
                    }
                    { !this.props.dismiss ? null :
                        <div className="u-large-hide u-medium-hide l-col l-col-small-12 u-spacing-m">
                            <OraButton type={"text"} onClick={this.props.dismiss}>Zmień salon</OraButton>
                        </div>
                    }
                </div>
            </div>
        </OraModal>);
    }
}


let mapStateToProps = state => ({
    errors: getPickupReplanishmentRequiredErrors(state),
    cartCode: getCartCode(state)
});
let mapDispatchToProps = dispatch => ({
    dismiss: () => dispatch(dismissBackendValidationErrors()),
    goToCart: () => dispatch(gotoCartSummary()),
    confirm: () => dispatch(confirmReplanishment())
});

const PickupReplanishmentErrorModal = connect(mapStateToProps, mapDispatchToProps)(PickupReplanishmentErrorView);
export default PickupReplanishmentErrorModal;
